.card {
  width: 240px;
  height: 320px;
  /* margin-left: 40px;
    margin-right: 40px;  */
  margin: 20px;
  /* display: flex;
    flex-direction: row; */
}
.card-img {
  width: auto;
  max-width: 300px;
  height: auto;
  max-height: 135px;
  min-height: 135px;
}
.card-body {
  align-content: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  order: 1;
  outline-color: #fafaed;
}

.card-title {
  /* font-size: 14px; */
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-subtitle {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  font-size: 12px;
  padding: 3px;
}

.eventName {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
  padding: 8px;
}
.eventMonth {
  font-size: 14px;
  text-align: center;
  color: #cf0f2e;
  min-width: 45px;
  float: left;
  background-color: rgb(241, 236, 236);
  /* border-style: solid; */
  /* border-color: rgb(218, 213, 213); */
}

.eventDay {
  font-size: 17px;
  color: #6e6e70;
  text-align: center;
  min-width: 45px;
  background-color: rgb(241, 236, 236);
}

.help {
  /* text-align: center; */
  font-size: 14px;
  margin-bottom: 30px;
}
